* {
  box-sizing: border-box;
  list-style-type: none;
}
button {
  border: none;
  background-color: none;
}
form {
  border: none;
  background-color: none;
}

body {
  margin: 0;
}
img {
  object-fit: cover;
  display: block;
}
a {
  color: inherit;
  text-decoration: none;
}
